@import "../../../styles/base";
@import "../../../styles/animation.scss";
.blueblob {
	position: absolute;
	left: 0;
	top: 0;
	width: 8em;
	z-index: -1;
	animation: jumpInTopLeft 800ms;
}
.deskbg {
	position: absolute;
	right: 0;
	bottom: 20px;
	z-index: -2;
	animation: jumpInBottom 800ms;
}

.deskft {
	position: absolute;
	right: 0;
	bottom: 0;
	z-index: -1;
	animation: jumpInBottom 850ms;
}

.dots {
	position: absolute;
	padding-bottom: -20px;
	left: -10%;
	top: -10%;
	width: 100px;
	z-index: -1;
	animation: fadeIn 2s;
}
.welcome {
	// position: fixed;
	padding-top: 2rem;
	height: min(100vh, 1000px);
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;

	&_text_br {
		display: none;
	}

	&_text_section {
		padding-right: 250px;
		position: relative;
		display: flex;
		flex-direction: column;
		margin-top: -2rem;
		width: 40%;
		height: 50%;
	}

	&_text_hello {
		position: relative;
		padding-bottom: 1.2rem;
		left: -20px;
		font-family: "caveat";
		font-size: 4vw;
		color: $font-gray;
		font-weight: 500;
		@include moveInDelay("700ms", "moveInTop", "1s");
	}

	&_text_intro {
		font-family: "ChenYuluoyan", "Noto Serif TC", serif;
		font-size: 2vw;
		color: $font-gray;
		font-weight: 400;
		width: 34vw;
		@include moveInDelay("1s", "moveInTop", "1s");
	}

	&_image {
		position: absolute;
		height: 25vw;
		// width: 30vw;
		right: 12vw;
		bottom: 15vw;
		background-color: transparent;
		animation: moveInRight 1s;
	}

	.cafe {
		width: 90px;
		height: auto;
		position: absolute;
		right: 120px;
		bottom: 150px;
	}

	img {
		height: 100%;
		@include moveInDelay("400ms", "moveInRight", "1s");
	}
}

