@include sm {
	.deskbg {
		bottom: 0px;
		z-index: -2;
	}

	.deskft {
		bottom: -10px;
		z-index: -1;
	}
	.welcome {
		padding-top: 7rem;
		flex-direction: column-reverse;
		justify-content: flex-end;
		position: relative;
		height: auto;

		&_text_section {
			display: flex;
			flex-direction: column;
			padding: 3rem 0 5rem;
			width: 75%;
			height: auto;
			align-items: center;
			justify-content: center;
		}
		&_text_br {
			display: inline;
		}
		&_text_intro {
			text-align: center;
			font-size: 24px;
			animation: moveInTop 1s;
			width: 100%;
		}

		&_text_hello {
			left: 0px;
			padding-bottom: 1rem;
			font-size: 40px;
			font-weight: 500;
			animation: moveInTop 1s;
		}
		&_image {
			position: relative;
			right: 10px;
			bottom: 20px;
			height: 220px;
			width: 220px;
			animation: moveInTop 1s;
		}

		.cafe {
			display: none;
		}

		img {
			height: 100%;
			animation: moveInTop 1s;
		}
	}
}
